//
export class Pay {
    //
    isChecked: boolean = false;
    //
    money: number;
}

//
 export class OrderDate {
     //
     payMode:string;
     //
     rechAmount : number;
     //01-,02-
     rechType : string;
     //01-02-iOS03-WEB04-
     sysType :string;
 }

 export class CreateOrderResponse {
    // id
    rechId: string;
    // apppayMode="02"
    alipayOrderStr: string;
    // app
    wechatOrder: object;
}

// 
export function getPayList(moneyList = [50, 100, 200, 300, 500, 1000]): Pay[] {
    let payList: Pay[] = [];

    for (let i = 0; i < moneyList.length; i++) {
        let checked;
        if(i==0){
            checked= true;
        }else{
            checked=false;
        }
        payList.push({

            isChecked:checked,
            money: moneyList[i]
        });
    }

    return payList;
}



