import { Component } from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {HomePage} from "../../home/home";

@Component({
    selector: 'page-finish_detail',
    templateUrl: 'finish_detail.html'
})
export class FinishDetailPage {
    picId: string;
    private task:any;

    constructor(public navCtrl: NavController,
                private param:NavParams) {
        this.task = param.get("task");
        //
        if(this.task.TYPE == '01'){
            this.task.TYPE_STR = "";
        }
        if(this.task.TYPE == '02'){
            this.task.TYPE_STR = "";
        }
        if(this.task.TYPE == '03'){
            this.task.TYPE_STR = "";
        }
        //
        if(this.task.BIG_TYPE == '11'){
            this.task.BIG_TYPE_STR = "";
        }
        if(this.task.BIG_TYPE == '12'){
            this.task.BIG_TYPE_STR = "";
        }
    }

    toHome(){//
        this.navCtrl.push(HomePage);
    }

}



