import {ChangeDetectorRef, Component, OnDestroy, OnInit} from '@angular/core';
import {AlertController, NavController, NavParams, Platform} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {ReleaseHistoryPage} from "../taskList/release_history";
import {UserService} from "../../../providers/user.service";
import {HomeService} from "../../../providers/home.service";
import {Geolocation} from "@ionic-native/geolocation";
import {TaskListPage} from "../taskList/taskList";
import {ReleasePage_4} from "./release_4";

declare let BMap: any;
declare let baidumap_location;

@Component({
    selector: 'page-release_add',
    templateUrl: 'release_address.html'
})
export class ReleasePage_Address implements OnInit, OnDestroy {
    alive: boolean;
    detail: string;//
    coin: string;//
    time: string;//
    remark : string;//
    taskInfo: any = {};//
    // 
    map: any;
    // 
    locationMarker: any;
    // 
    private locationPoint: any;

    private province:any;//
    private city:any;//
    private area:any;//
    private selectArea;//

    constructor(public navCtrl: NavController,
                private param: NavParams,
                private taskService: TaskService,
                private homeService: HomeService,
                private geolocation: Geolocation,
                public  cd :ChangeDetectorRef,
                public alertCtrl: AlertController,
                private platform: Platform) {
        this.taskInfo  = param.get("taskInfo");
    }

    ngOnInit(): void {
        this.homeService.loadScript()
            .subscribe(() => {
                // 
                this.locating();
            });
    }

    // 
    ionViewWillEnter(): void {
        this.alive = true;
    }

    ionViewDidLeave(): void {
        this.alive = false;
    }

    // 
    ngOnDestroy(): void {
        this.alive = false;
    }

    releaseTask(){//
        this.taskService.releaseTask(this.taskInfo).subscribe(data => {
            //
            this.navCtrl.push(ReleaseHistoryPage);
        }, CommonUtil.emptyCallback);
    }

    private locating(): void {
        console.log('...');

        // API
        this.map = new BMap.Map(document.getElementById('bmap'));
        var geolocation = new BMap.Geolocation();

        let that:any = this;
        geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == "0"){//0
                that.province = r.address.province ;
                that.city = r.address.city;
                //
                that.taskService.getArea(that.city).subscribe(data => {
                    that.area = data;
                    that.cd.detectChanges();
                }, CommonUtil.emptyCallback);
            }
            else {
                //alert('failed'+this.getStatus());
            }
        },{enableHighAccuracy: true});

    }

    //
    private catchData(areaStr:any){
        this.selectArea = areaStr;
    }

    releaseNext(){//
        if(this.selectArea == "" || this.selectArea == null){
            this.showAlert('');
            return;
        }
        var areaData = "";//
        areaData = this.province + this.city + this.selectArea;
        this.navCtrl.push(ReleasePage_4,{areaData:areaData,taskInfo:this.taskInfo});
    }

    //
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }

}



